/* ---------- GENERIC STYLING ---------- */


body {
  padding-left: 10px;
  padding-right: 10px;
  overflow-x: hidden !important;
  font-family: "Oswald", sans-serif !important;
  background-color: $background-color;
  color: $text-color;
  margin: 0;
}




.MuiGrid-container {
  margin: $auto;
}

.MuiButton-root,
.MuiFormLabel-root,
.MuiInputBase-input{
  font-family: $font-stack1;
}
.main-button {
  font-size: clamp(20px, 2vw , 25px) !important;
  font-family: $font-stack1;
  color: $button-color;
  border: 1px solid !important;
}

.MuiTypography-h1 {
  font-size: clamp(75px, 8vw , 100px) !important;
  font-family: $font-stack1;
  color: $secondary;
  
}
.MuiTypography-h2 {
  font-size: clamp(50px, 7vw , 70px) !important;
  color: $primary;
  font-family: $font-stack1;

}
.MuiTypography-h3 {
  font-size: clamp(40px, 4.2vw , 50px) !important;
  font-family: $font-stack1;
  color: $secondary;
}
.MuiTypography-h4 {
  font-family: $font-stack1;
  font-size: clamp(30px, 3.7vw , 40px) !important;
  color: $secondary;
  
}
.MuiTypography-h5 {
  font-family: $font-stack1;
  font-size: clamp(25px, 3.25vw, 35px) !important;
  color: $primary;

}
.MuiTypography-h6 {
  font-family: $font-stack2;
  font-size: clamp(25px, 3vw , 25px) !important;
  color: $tertiary;

}

.MuiTypography-subtitle1 {
  font-family: $font-stack1;
  font-size: clamp(15px, 2vw , 20px) !important
}

.MuiTypography-subtitle2 {
  font-family: $font-stack2;
  font-size: clamp(25px, 3vw , 35px) !important;
  color: $secondary;

}

.MuiTypography-caption {
  font-family: $font-stack1;
  font-size: clamp(20px, 2vw , 25px) !important;
  color: $secondary;

}
  .MuiTypography-body1 {
    font-family: $font-stack1;
    font-size: clamp(15px, 2vw , 20px) !important;
    color: $secondary;
  }

#nav {
  color: $text-color;

  background-color: $nav-background;


  .nav-title {
    flex-grow: 1;
  }
  .nav-item {
    color: $nav-item-color;
    padding-right: 5vw !important;
    cursor: pointer;
    &:hover {
      color: $nav-color-hover;
    }
  }

  .nav-item-active {
    color: $nav-item-active-color;
  }

}

#top {
  #hi {
    color: $primary;
    font-family: $font-stack2;
  }

  min-height: 100vh;
  margin-top: 10vh !important;

  .MuiTypography-h1 {
    padding-bottom: 10vh;
  }

  #caption {
    padding-bottom: 10vh;
    
  }

}

#about {
  min-height: 100vh;
  .school-logo {
    margin-bottom: 0px !important;
    padding-bottom: 0 !important;
    min-height: 45px;
    height: 8vw;
    max-height: 60px;

    filter: $filter ;

  }
  
  

  [class^="devicon-"] {
    font-size: clamp(10px,4rem, 70px) !important;

    color: $devicon;
    will-change: box-shadow !important;
    text-align: center;
    // @include animation('glow 5s ease-in-out infinite alternate;');
    
  
  }


  .logo {

    filter: $filter;

    border-radius: 10%;
    width: clamp(10px,20vw, 90px) !important;
   
  }

  
}

#work {


    margin-top: 20vh !important;
  .active-job {
    vertical-align: middle !important;
    // height: 10vh !important;
    border: 1px solid $button-color;
    
  }
  .work-image {

      height: 8vw;
      max-height: 60px;
      filter: $filter;
  }

  .job-descriptions {
    // margin-top: 0 !important;
    // padding-top: 0 !important;
    min-height: 50vh;
    // min-height: 80vh;
  }

  .main-list {
    margin-left: 2vh;
    list-style-type: disc;
    
  }
  .sub-list {
    list-style-type: circle;
    font-size: 1.4rem;
    margin-left: 5vh;
  }
}

#project {

  .jv-logo {
    height: 4.5vh;
    filter: $filter;
  }

  .jv-video {
    padding-right: 5vh !important;
    height: 40vh;

  }

  .market-link {
   
    width: 30vw;
    max-width: 150px;
    border: 1px solid white!important;
    border-radius: 2%;
  }

  .jb-video {
    height: 40vw;
    max-height: 300px;
  }

  .paint {
    height: 40vw;
    max-height: 350px;
  }

}

#project-archive {
  word-wrap: break-word !important;
  .table-heading {
    color: red !important !important !important;
    font-weight: bold;
    font-size: large;
  }

 
  .MuiSvgIcon-root {
    color: $text-color;
    cursor: pointer;

    :hover {
      color: $button-color;
    }
  }
}
.table-row{
  padding-top: 1vh;
  padding-bottom: 1vh;
    // border-top: 1px solid;

}

