//  .table-cell-2 {

//  }

//  .table-cell-3 {

// }


#footer {
  margin-top: 10vh !important; 
  margin-bottom: 2vh !important;
  .MuiSvgIcon-root {
    font-size: 3rem;
    color: $text-color;
    cursor: pointer;

    :hover {
      color: $button-color;
    }
  }
}